.filter{width:270px;background-color:#fff;display:flex;flex-direction:column;gap:3rem}.filter__title{margin-bottom:1rem;font-family:"Poppins",sans-serif;font-weight:600;font-size:1.125rem;line-height:1.65rem}.filter__switcher{display:none;padding:0 .5rem;font-family:"Poppins",sans-serif;font-weight:500;font-size:.95rem;line-height:1.375rem;color:#151515;align-items:center;gap:.5rem;background:inherit;border:none;border-radius:12px;cursor:pointer;transition:all .1s linear}.filter__switcher:hover{background-color:#d2d2d4}.filter__switcher img{transition:all .3s linear}.filter-categories{list-style:none;display:flex;flex-direction:column;gap:.75rem}.filter-categories__item{display:flex;justify-content:space-between;align-items:center;font-size:.875rem;line-height:1.185rem;color:#151515;cursor:pointer}.filter-categories__item p{text-overflow:ellipsis;overflow:hidden;white-space:nowrap}.filter-categories__item:hover p{text-decoration:underline}.filter-categories__item span{min-width:30px;padding:0 8px;background:#f4f8ec;border-radius:12px;text-align:center;font-family:"Poppins",sans-serif;font-weight:600;font-size:.75rem;line-height:1.185rem;color:#6a983c}.filter-categories__item--active{font-weight:700}.filter-brands{list-style:none;display:flex;flex-direction:column;gap:.75rem;max-height:230px;overflow-y:auto;list-style:none}.filter-rating{list-style:none;display:flex;flex-direction:column;gap:.75rem}.filter__label{width:100%;position:relative;padding:1px 0 1px 30px;display:flex;font-size:14px;line-height:19px;cursor:pointer}.filter__label p{text-overflow:ellipsis;overflow:hidden;white-space:nowrap;width:100%}.filter__input{-webkit-appearance:none;appearance:none;width:20px;height:20px;margin:0;border:2px solid #d1d1d1;border-radius:3px;background-color:#fff;outline:none;cursor:pointer;position:absolute;top:50%;left:2px;z-index:3;transform:translateY(-50%)}.filter__input:hover{box-shadow:0 0 5px #6a983c}.filter__input:checked{background-color:#6a983c;border:2px solid #46760a}.filter__input:checked::before{content:"✔";font-size:.9rem;color:#fff;position:absolute;right:2px;top:-3px}.filter__reset{width:fit-content;margin:0 auto;border:none;background:inherit;font-family:"Poppins",sans-serif;font-weight:700;font-size:.95rem;line-height:1.375rem;color:#a9a9a9;cursor:pointer;transition:all .1s linear}.filter__reset:hover{color:#575757}@media(max-width: 991.98px){.filter{padding:.75rem;border:1px solid #d1d1d1;box-shadow:rgba(99,99,99,.5) 0px 2px 8px 0px;position:absolute;top:0;left:0;bottom:0;z-index:4;overflow-y:auto;overflow-x:hidden}.filter__wrapper{display:none;position:fixed;width:100%;height:100%;top:0;left:0;z-index:3;backdrop-filter:blur(5px)}.filter__wrapper--show{display:flex}.filter__switcher{display:flex;position:absolute;top:-40px;left:0;z-index:2}}